___CSS_LOADER_EXPORT___.push([module.id, `.settings-container {
  padding: 20px;
  margin: 0 auto;
  background-color: #2e2e2e;
  color: #ffffff; 
  border-radius: 10px;
}

.settings-section {
  margin-bottom: 30px;
}

.settings-section h2 {
  margin-bottom: 15px;
  color: #f8f8ff;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
}

.form-group input[type="text"],
.form-group input[type="email"] {
  width: 100%;
  padding: 10px;
  border: none; 
  border-radius: 5px;
  background-color: #444;
  color: #ffffff;
}

.form-group input[type="text"]::placeholder,
.form-group input[type="email"]::placeholder {
  color: #bbbbbb;
}

.form-group input[type="checkbox"] {
  margin-right: 10px;
}
